import { useState, useEffect, useCallback, FC, FormEvent } from 'react';

import { useAppSelector, useAppDispatch } from "../../../hooks/hooks";
import useInputs from '../../../hooks/use-inputs';
import { updateUser } from '../../../services/actions/auth-actions';
import { Button, Input } from '@ya.praktikum/react-developer-burger-ui-components';
import style from './profile-form.module.css';

const ProfileForm: FC = () => {
  const dispatch = useAppDispatch();

  const { user } = useAppSelector((store) => store.auth);
  const { values, handleValues, setValues } = useInputs({ name: user.name, email: user.email, password: "" });
  const [disabledButton, setDisabledButton] = useState(false);

  const handleUpdate = useCallback(
    (e: FormEvent<HTMLFormElement>) => {
      e.preventDefault();
    dispatch(updateUser(values.name, values.email, values.password));
    setDisabledButton(!disabledButton);
  },
  [values, disabledButton]
);

  const handleReset = () => {
    setValues({ name: user.name, email: user.email, password: "" });
  };

  useEffect(() => {
    setDisabledButton(!disabledButton);
  }, [values]);

  return (
    <form className={style.form} onSubmit={(e) => handleUpdate(e)}>
      <Input
        type="text"
        name="name"
        value={values.name}
        placeholder=""
        icon="EditIcon"
        onChange={handleValues}
        error={false}
        errorText={""}
      />
      <Input
        type="email"
        name="email"
        value={values.email}
        placeholder=""
        icon="EditIcon"
        onChange={handleValues}
        error={false}
        errorText={""}
      />
      <Input
        type="password"
        name="password"
        value={values.password}
        placeholder=""
        icon="EditIcon"
        onChange={handleValues}
        error={false}
        errorText={""}
      />
      <div className={style.buttons}>
        <Button
          htmlType="button"
          type="secondary"
          size="medium"
          onClick={handleReset}
          disabled={!disabledButton}
        >
          O
        </Button>
        <Button
          htmlType="submit"
          type="primary"
          size="medium"
          disabled={!disabledButton}
        >
          
        </Button>
      </div>
    </form>
  );
}

export default ProfileForm;